export default function detectCollision(ball, gameObject) {
  let ballTop = ball.position.y;
  let ballBottom = ball.position.y + ball.size;
  let ballLeft = ball.position.x;
  let ballRight = ballLeft + ball.size;

  let gameObjectTop = gameObject.position.y;
  let gameObjectBottom = gameObjectTop + gameObject.size.h;
  let gameObjectLeft = gameObject.position.x;
  let gameObjectRight = gameObjectLeft + gameObject.size.w;

  const condition1 = ballBottom >= gameObjectTop && ballTop <= gameObjectBottom;

  const condition2 = ballLeft >= gameObjectLeft && ballRight <= gameObjectRight;
  const condition = condition1 && condition2;
  if (condition) {
    return true;
  } else {
    return false;
  }
}
