import Paddle from "./paddle";
import InputHandler from "./input";
import Ball from "./ball";
import { buildLevel, levels } from "./levels";

const GAME_STATE = {
  PAUSED: 0,
  RUNNING: 1,
  MENU: 2,
  GAME_OVER: 3,
  NEW_LEVEL: 4,
  VICTORY: 5
};

export default class Game {
  constructor(game) {
    this.gameWidth = game.gameWidth;
    this.gameHeight = game.gameHeight;
    this.gameState = GAME_STATE.MENU;

    this.gameObjects = [];
    this.bricks = [];

    this.paddle = new Paddle(this);
    this.ball = new Ball(this);
    this.lives = 3;
    this.levels = levels;
    this.level = 0;
    new InputHandler(this.paddle, this);
  }

  start() {
    if (
      this.gameState === GAME_STATE.MENU ||
      this.gameState === GAME_STATE.NEW_LEVEL
    ) {
      if (this.levels[this.level]) {
        this.bricks = buildLevel(this, this.levels[this.level]);
        this.gameObjects = [this.paddle, this.ball];
        this.ball.reset();
        this.gameState = GAME_STATE.RUNNING;
      } else {
        this.gameState = GAME_STATE.VICTORY;
      }
    }
  }

  restart() {
    this.gameObjects = [];
    this.bricks = [];
    this.lives = 3;
    this.level = 0;
    this.bricks = buildLevel(this, this.levels[this.level]);
    this.gameObjects = [this.paddle, this.ball];
    this.ball.reset();
    this.gameState = GAME_STATE.RUNNING;
  }

  togglePause() {
    if (this.gameState === GAME_STATE.RUNNING) {
      this.gameState = GAME_STATE.PAUSED;
    } else {
      this.gameState = GAME_STATE.RUNNING;
    }
  }

  update(deltaTime) {
    if (this.lives === 0) {
      this.gameState = GAME_STATE.GAME_OVER;
    }

    if (this.gameState === GAME_STATE.RUNNING && this.bricks.length === 0) {
      this.level++;
      this.gameState = GAME_STATE.NEW_LEVEL;
      this.start();
    }

    if (this.gameState === GAME_STATE.RUNNING) {
      [...this.gameObjects, ...this.bricks].forEach((elem) =>
        elem.update(deltaTime)
      );
      this.isInLoop = true;

      this.bricks = this.bricks.filter((brick) => !brick.markedForDeletion);
    }
  }

  draw(ctx) {
    if (
      this.gameState === GAME_STATE.RUNNING ||
      this.gameState === GAME_STATE.PAUSED
    ) {
      [...this.gameObjects, ...this.bricks].forEach((elem) => elem.draw(ctx));
    }

    if (this.gameState === GAME_STATE.PAUSED) {
      ctx.rect(0, 0, this.gameWidth, this.gameHeight);
      ctx.fillStyle = "rgba(225, 225, 225, 0.5)";
      ctx.fill();

      ctx.font = "30px Roboto";
      ctx.fillStyle = "#0ff";
      ctx.textAlign = "center";
      ctx.fillText("PAUSED", this.gameWidth / 2, this.gameHeight / 2);
    }
    if (this.gameState === GAME_STATE.MENU) {
      ctx.rect(10, 10, this.gameWidth - 20, this.gameHeight - 20);
      ctx.fillStyle = "#fff";
      ctx.fill();

      ctx.font = "30px Roboto";
      ctx.fillStyle = "#0ff";
      ctx.textAlign = "center";
      ctx.fillText(
        "Press SPACEBAR to start.",
        this.gameWidth / 2,
        this.gameHeight / 2
      );
    }
    if (this.gameState === GAME_STATE.GAME_OVER) {
      ctx.rect(10, 10, this.gameWidth - 20, this.gameHeight - 20);
      ctx.fillStyle = "#f00";
      ctx.fill();

      ctx.font = "30px Roboto";
      ctx.fillStyle = "#0ff";
      ctx.textAlign = "center";
      ctx.fillText(
        "GAME OVER. Press Ctrl + Alt + R to restart",
        this.gameWidth / 2,
        this.gameHeight / 2
      );
    }
    if (this.gameState === GAME_STATE.VICTORY) {
      ctx.rect(10, 10, this.gameWidth - 20, this.gameHeight - 20);
      ctx.fillStyle = "#0ff";
      ctx.fill();

      ctx.font = "30px Roboto";
      ctx.fillStyle = "#fff";
      ctx.textAlign = "center";
      ctx.fillText(
        "VICTORY! Press Ctrl + Alt + R to restart",
        this.gameWidth / 2,
        this.gameHeight / 2
      );
    }
  }
}
