export default class InputHandler {
  constructor(paddle, game) {
    document.addEventListener("keydown", (event) => {
      switch (event.key) {
        case "ArrowLeft":
          paddle.start();
          paddle.moveLeft();
          break;

        case "ArrowRight":
          paddle.start();
          paddle.moveRight();
          break;

        case "Escape":
          game.togglePause();
          break;

        case " ":
          game.start();
          break;

        default:
          break;
      }

      if (event.ctrlKey && event.altKey && event.key === "r") {
        game.restart();
      }
    });

    document.addEventListener("keyup", (event) => {
      switch (event.key) {
        case "ArrowLeft":
          paddle.stop();
          break;

        case "ArrowRight":
          paddle.stop();
          break;

        default:
          break;
      }
    });
  }
}
